'use client';
import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { supabase } from '@/lib/supabaseClient';
import type { User } from '@supabase/supabase-js';
import './CSS/d.css';

type Article = {
  title: string;
  description?: string;
  url?: string;
  source?: string;
};

export default function Dashboard() {
  const router = useRouter();

  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [news, setNews] = useState<Article[]>([]);
  const [newsLoading, setNewsLoading] = useState(true);

  useEffect(() => {
    const checkSession = async () => {
      const { data: { session } } = await supabase.auth.getSession();
      if (!session) {
        router.push('/login');
      } else {
        setUser(session.user);
      }
      setLoading(false);
    };
    checkSession();
  }, [router]);

  useEffect(() => {
    let mounted = true;
    setNewsLoading(true);

    fetch('/api/news')
      .then(async (res) => {
        if (!res.ok) {
          const err = await res.json().catch(() => ({}));
          throw new Error(err?.error || 'Error al cargar noticias');
        }
        return res.json();
      })
      .then((data) => {
        if (!mounted) return;
        const raw = data.articles || [];
        // normalizar varias respuestas posibles
        const norm = raw.map((a: any) => ({
          title: a.title || a.headline || a.title || '',
          description: a.description || a.summary || a.excerpt || '',
          url: a.link || a.url || a.clean_url || a.resolved_url || '',
          source: (a.source && (a.source.name || a.source)) || a.clean_url || a.source || ''
        }));
        setNews(norm);
      })
      .catch((err) => {
        console.error('fetch /api/news error', err);
      })
      .finally(() => {
        if (mounted) setNewsLoading(false);
      });

    return () => { mounted = false; };
  }, []);

  if (loading) return <p>Cargando...</p>;
  if (!user) return null;

  return (
    <div className="dashboard-container">
      <button className="menu-toggle" onClick={() => setSidebarOpen(!sidebarOpen)}>
        ‚ò∞
      </button>

      <aside className={`sidebar ${sidebarOpen ? 'open' : ''}`}>
        <img
          src={user.user_metadata?.avatar_url || 'https://via.placeholder.com/80'}
          alt="Foto de perfil"
          className="profile-img"
        />
        <h2 className="username">{user.user_metadata?.full_name || 'Usuario'}</h2>

        <nav className="nav-menu">
          <button className="nav-btn">Opci√≥n 1</button>
          <button className="nav-btn">Opci√≥n 2</button>
          <button className="nav-btn">Opci√≥n 3</button>
          <button className="nav-btn">Opci√≥n 4</button>
          <button className="nav-btn">Opci√≥n 5</button>
        </nav>

        <button
          onClick={async () => {
            await supabase.auth.signOut();
            router.push('https://candigo.vercel.app/');
          }}
          className="logout-btn"
        >
          Cerrar sesi√≥n
        </button>
      </aside>

      <main className="main-content">
        <h1>Bienvenido al Dashboard</h1>
        <p>¬°Has iniciado sesi√≥n correctamente!</p>

        <section className="novedades-section">
          <h2>üì∞ Novedades Pol√≠ticas (M√©xico)</h2>
          {newsLoading ? (
            <p>Cargando noticias...</p>
          ) : news.length === 0 ? (
            <p>No se encontraron noticias por el momento.</p>
          ) : (
            news.map((n, i) => (
              <div key={i} className="news-item">
                <h3>{n.title}</h3>
                <p>{n.description}</p>
                <a href={n.url || '#'} target="_blank" rel="noopener noreferrer">Leer m√°s</a>
                {n.source ? <small> ‚Äî {n.source}</small> : null}
              </div>
            ))
          )}
        </section>

        <section className="candidatos-section">
          <h2>üë§ Candidatos</h2>
          <div className="candidatos-grid">
            <div className="candidato">
              <img src="https://via.placeholder.com/300x200" alt="Candidato 1" />
              <h3>Mar√≠a L√≥pez</h3>
              <p>Partido: Progreso Unido</p>
            </div>
            <div className="candidato">
              <img src="https://via.placeholder.com/300x200" alt="Candidato 2" />
              <h3>Carlos P√©rez</h3>
              <p>Partido: Fuerza Ciudadana</p>
            </div>
            <div className="candidato">
              <img src="https://via.placeholder.com/300x200" alt="Candidato 3" />
              <h3>Ana Torres</h3>
              <p>Partido: Renovaci√≥n Nacional</p>
            </div>
          </div>
        </section>
      </main>
    </div>
  );
}
